.wrap-main-nav {
    background: var(--white);
    z-index: 991;
    transition: .5s all;
    .icon-svg {
        color: #fff;
    }
    .home{
        img{width: 18px;}
    }

    &.pin {
        box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
        background: #000;
        &>.container {
            border-bottom: 0;
        }
    }

    .logo {
        top: 12px;
        position: absolute;
        display: none;
    }

    &>.container {
        align-items: center;
        padding: 15px;
        margin-bottom: 15px;
        background: #000;
    }

    .main-nav {
        text-align: center;

        .parent {
            display: flex;
            justify-content: center;
            padding-left: 10px;

            li {
                display: inline-block;
                padding: 0 10px;
                a {
                    text-transform: uppercase;
                    font: bold 14px var(--font1);
                    color: #fff;
                }

                // &.active {
                //     a {
                //         font-weight: bold;
                //         color: var(--ione-color);
                //     }
                // }

                &:hover {
                    a {
                        color: var(--ione-color);
                    }
                }
            }

            @media (max-width: 1200px) {
                li {
                    a {
                        font-size: 13px;
                        padding: 0 7px
                    }
                }
            }
        }

        .advertising-cooperation {
            display: none;
        }
    }

    .right-head {
        margin-top: -5px;
        position: absolute;
        right: 10px;
        top: 15px;
    }

    .search-btn {
        display: inline-block;
        cursor: pointer;
        height: 19px;
        position: relative;

        input {
            display: none;
            position: absolute;
            background: var(--bg-agray2);
            border-radius: 59px;
            border: 0;
            width: 214px;
            font-size: 12px;
            transition-duration: 400ms;
            transition-property: all;
            transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
        }

        button {
            border: 0;
            background: none;
        }

        .icon-svg {
            position: relative;
            display: block;
            top: 7px;
            -webkit-transition-duration: 400ms;
            transition-duration: 400ms;
            -webkit-transition-property: all;
            transition-property: all;
            -webkit-transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
            transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
        }

        .close {
            width: 11px;
            height: 11px;
            opacity: 0;
        }

        &.is-clicked {
            .icon-svg {
                top: -13px;
                opacity: 0;
            }

            .close {
                opacity: 1;
                top: -6px;
            }
        }
    }

    .search-wrap {
        background: #e5e5e5;
        position: absolute;
        top: 50px;
        width: 300px;
        right: 0;
        padding: 10px;
        z-index: 2;
        display: none;
        box-shadow: 0 6px 12px rgba(0, 0, 0, .1);
        -moz-box-shadow: 0 6px 12px rgba(0, 0, 0, .1);

        input {
            font-size: 14px;
            height: 35px;
            width: 100%;
            outline: 0;
            border: 0;
            padding: 0 10px;
            border-radius: 3px;
        }

        button {
            position: absolute;
            right: 10px;
            width: 35px;
            height: 35px;
            background: none;

            svg {
                color: #666
            }
        }

        @media (max-width: 576px) {
            width: 100%;
        }
    }

    @media (max-width: 1200px) {
        &>.container {
            padding: 15px 10px;
        }

        .right-head {
            right: 10px;
        }
    }


    @media (max-width: 1025px) {
        
        &>.container {
            padding: 10px 15px;
            border-bottom: 1px solid #e5e5e5;
            background: #fff;
        }
        .icon-svg{color: #222;}
        .home {
            display: none;
        }

        .logo {
            left: 50%;
            margin-left: -57px;
            display: block;
        }

        .right-head {
            right: 15px;
        }

        .all-menu-tablet {
            display: block;
        }

        .main-nav {
            text-align: left;
            width: 100%;
            left: 0;
            background: #fff;
            padding: 0 10px;
            box-shadow: 0 1px 3px rgba(80, 80, 80, .2), 0 3px 5px rgba(80, 80, 80, .15);
            position: absolute;
            top: calc(100% + 10px);
            opacity: 0;
            visibility: hidden;
            transition-duration: 200ms;
            transition-property: opacity, top, visible;
            transition-timing-function: cubic-bezier(.7, 1, .7, 1);

            .parent {
                display: flex;
                flex-wrap: wrap;
                padding-left: 0;

                li {
                    width: 100%;
                    padding: 12px 0;
                    border-bottom: 1px solid #e5e5e5;

                    a {
                        font-size: 14px;color: #222;
                    }
                }
            }

            .advertising-cooperation {
                display: block;
                background: var(--red);
                text-align: center;
                color: #fff;
                font-weight: bold;
                padding: 10px;
                text-transform: uppercase;
                border-radius: 3px;
                margin: 20px 0;
            }

            &::after {
                width: 100%;
                height: 100vh;
                position: absolute;
                background: rgba(0, 0, 0, .75);
                bottom: -100vh;
                left: 0;
                content: '';
                z-index: -1;
                opacity: 0;
                visibility: hidden;
                transition-duration: 700ms;
                transition-property: all;
                transition-timing-function: cubic-bezier(.7, 1, .7, 1);
            }

            &.show-all-menu {
                top: 100%;
                visibility: visible;
                opacity: 1;

                &::after {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
    }
}

.all-menu-tablet {
    float: left;
    width: 50px;
    height: 33px;
    text-align: center;
    margin-left: -15px;
    margin-top: -2px;
    cursor: pointer;
    display: none;

    .hamburger {
        width: 20px;
        height: 2px;
        background: #666;
        display: inline-block;
        position: relative;

        &:before,
        &:after {
            content: '';
            width: 20px;
            height: 2px;
            background: #666;
            position: absolute;
            left: 0;
        }

        &:before {
            top: 6px;
        }

        &:after {
            top: 12px;
        }
    }

    &.close-menu-tablet {
        padding-top: 5px;

        .hamburger {
            transform: rotate(45deg);
            -webkit-transform: rotate(45deg);

            &:before {
                display: none;
            }

            &:after {
                transform: rotate(-90deg);
                -webkit-transform: rotate(-90deg);
                top: 0;
            }
        }
    }

}

.advertising-cooperation {
    text-align: right;
    margin-bottom: 5px;
    margin-top: 5px;

    a {
        color: var(--red);
        font-weight: bold;
    }

    @media (max-width: 1025px) {
        display: none;
    }
}

.banner-top-home {
    display: flex;
    justify-content: space-between;
    width: 100%;
    transition: none 0s ease 0s;

    .banner-mobile {
        display: none;
        width: 100%;

        img {
            width: 100%;
        }
    }

    @media (max-width: 1025px) {
        justify-content: center;

        .logo {
            display: none;
        }
    }

    @media (max-width: 576px) {
        padding: 0 !important;

        .banner_pc {
            display: none;
        }

        .banner-mobile {
            display: block;
        }
    }
}