.footer {
    background: #171b23;
    color: #fff;
    padding: 30px 0;
    p{margin-bottom: 5px;}
    .footer_top{
        h3{
            font: bold 16px var(--font1);
            margin-bottom: 15px;
        }
        .col{
            width: calc((100% - 140px)/3);padding-left: 25px;
            margin-top: 15px;
        }
        .page{
            text-align: right;
        }
    }
    .nav{
        ul{
            column-count:2;
            li{
                position: relative;
                margin-bottom: 12px;
                padding-left: 10px;
                &::before{
                    content: "";
                    background: var(--green);
                    width: 4px;
                    height: 4px;
                    position: absolute;
                    left: 0;
                    top: 7px;
                }
                a{
                    color: #fff;
                }
            }
        }
    }
    .social{
        margin-top: 10px;
        a{
            margin-right: 3px;
        }
    }
    .footer_copyright{
        text-align: center;
        color: #737a87;
        font-size: 12px;
        border-top: 1px solid rgba(115, 122, 135, 0.4);
        padding-top: 40px;
        margin-top: 40px;
    }
    @media (max-width: 1025px) {
        .footer_top{
            .col{
                width: calc((100% - 140px)/2);
                margin-bottom: 20px;
            }
            .fan-page{
                display: none;
            }
        }
    }
    @media (max-width: 576px) {
        padding: 10px 0;
        .footer_top{
            .logo{
                width: 100%;
                text-align: center;
            }
            .col{
                width: 100%;
                margin-bottom: 20px;
                padding-left: 0;
            }
        }
        .footer_copyright{
            text-align: left;
            padding-top: 20px;
            margin-top: 5px;
        }
    }
}
